<template>
  <div>
    <h3 class="mb-5">Comentarios:</h3>
    <v-card elevation="3">
      <small class="red--text pa-5 my-0">
        {{ parrafo }}
      </small>
      <v-list>
        <v-list-item-group color="primary">
          <v-list-item v-if="!comentarios">
            <v-list-item-content>
              <v-list-item-title
                >No hemos podido encontrar comentarios
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <template v-for="(item, j) in comentarios">
            <v-list-item :key="j">
              <v-list-item-content>
                <v-list-item-title v-text="item"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider :key="j"> </v-divider>
          </template>
        </v-list-item-group>
      </v-list>
    </v-card>
  </div>
</template>

<script>
export default {
  props: ["comentarios", "coincidencias"],
  computed: {
    parrafo() {
      return `Se han encontrado ${
        this.coincidencias ? this.coincidencias : 0
      } mensajes relacionados con términos de fraude. Te recomendamos leer algunos de ellos.😉`;
    },
  },
};
</script>